<template>
  <div class="container">
    <h1 class="my-4">Iniciar Sesion</h1>
    <b-form @submit="login">
      <b-form-group label="GamerTag:" class="col-md-6 my-5">
        <b-form-input v-model="usuario.usuario_user" placeholder="Ingresa tu gamertag"></b-form-input>
      </b-form-group>

      <b-form-group label="Contrase単a:" class="col-md-6">
        <b-form-input v-model="usuario.password" type="password" placeholder="Ingresa tu contrase単a"></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="success" class="mx-2 my-4">Iniciar sesion</b-button>
      <b-button :to="{name: 'Register'}" variant="primary">Registrarse</b-button>
    </b-form>
    <div v-if="mensaje !== ''">
      <p>{{mensaje}}</p>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      usuario: { usuario_user: "", password: "" },
      mensaje: ""
    };
  },
  methods:{
    ...mapMutations(['obtenerUsuario']),
    ...mapActions(['guardarUsuario', 'leerToken', 'cerrarSesion']),
    login(){
      this.axios.post('/login', this.usuario)
      .then(res =>{
        const token =res.data.token;
        this.guardarUsuario(token);
      })
      .catch(e => {
          console.log(e.response);
          this.mensaje = e.response;
        })
    }
  }
};
</script>